





import { Component, Vue } from 'vue-property-decorator'

@Component({
  components: {}
})
export default class MyComponent extends Vue {
  /**computed */
  get visible() { // 展示参数控制方法
    return location.port !== "" || location.host === "doudizhu.github.io";
  }
  /**method */
  showClose() {
    this.$createDialog({
      type: 'prompt',
      title: '域名配置',
      content: '默认为localhost:8080,线上调试可填写域名（如api.daokoujihua.com），开发可填写本地api服务地址（如10.10.2.22:8080)',
      prompt: {
        value: this.$store.state.domainApi,
        placeholder: '输入api域名配置'
      },
      onConfirm: (e:any, promptValue:any) => {
        this.$store.commit('setDomainApi',promptValue)
        this.$createToast({
          type: 'warn',
          time: 2000,
          txt: `请求api域名已修改为: ${promptValue || ''}`
        }).show()
      }
    }).show()
  }
}
