import axios, { AxiosResponse, AxiosRequestConfig } from 'axios'
import { Toast } from 'cube-ui'
import store from '@/store'

// 创建axios的实例
const service = axios.create({
  timeout: 1000000 // 超时时间
})

// 请求拦截
service.interceptors.request.use(
  (config: AxiosRequestConfig) => {
    (Toast as any).$create({
      time: 0 // 设置为0时永不关闭，默认为3000
    }).show()
    return config;
  },
  (err: any) => {
    Promise.reject(err);
  }
);

// 响应拦截
service.interceptors.response.use(
  (response: AxiosResponse) => {
    // console.log('response',response);
    (Toast as any).$create().hide(); // 关闭请求中弹窗
    return response.data;
  },
  (err: any) => {
    (Toast as any).$create({
      type: 'txt',
      txt: `请求错误: ${err || err.response}`
    }).show()
    // console.log('err',err)
    // console.log('err.response',err || err.response)

    return err.response
  }
)


// export default service

/**
 * 请求插件
 * @param {*} options 
 */
export default function request(options: any) {
  const {
    // api 请求方式
    api,
    params,
    method = 'post',

    // axios 原生请求方式
    callback, // 直接回调方法。非async模式
    url, // 全路径url
    direct, // 相对路径
    data, // 未经处理的数据

    // 表单中间页 请求方式
    formSubmitData,
    withCredentials = false, // 允许携带cookie
  } = options

  
  const port = location.port
  let host = port ? location.hostname + ':5000' : location.host // 默认值：本地开发环境&调试环境
  if(store.state.domainApi){ // 设定值：调试环境
    host = store.state.domainApi
  }
  const protocol = (host === 'api.daokoujihua.com')? 'http://' : ''; // 兼容道口计划协议

  return (service as any)[method](
    // url
     url ? url : `${protocol}//${host}/` + (direct || 'api/3.0/m/' + api),
    // params
    data ? data : dealFormData({
      params: encodeURI(JSON.stringify(params)), // 防止中文编码错误采用encodeURI处理
      from: 'W',
      imei: 'imei',
      appKey: '88888888',
    }),
  )
}

function dealFormData(data: any) {
  let keys = Object.keys(data),
    dataStr = '';
  keys.forEach((curr, index) => {
    dataStr += `${curr}=${data[curr]}&`
  })
  return dataStr.slice(0, -1)
}