import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'

Vue.use(Router)

export const routes = [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/user',
      component: { render(c: any) { return c('router-view') } },
      children: [
        {
          path: 'login',
          component: () => import('./views/user/Login.vue'),
          name: 'userLogin',
        },
        {
          path: 'register',
          component: () => import('./views/user/Register.vue'),
        },
      ]
    },
    {
      path: '/account',
      component: { render(c: any) { return c('router-view') } },
      children: [
        {
          path: 'shAccount',
          component: () => import('./views/account/ShAccount.vue'),
        },
      ]
    },
    {
      path: '/project',
      component: { render(c: any) { return c('router-view') } },
      children: [
        {
          path: 'list',
          component: () => import('./views/project/List.vue'),
        },
      ]
    },
    {
      path: '*',
      redirect: location.host === "doudizhu.github.io" ? '/user/login' : '',
      name: '/404',
      component: ()=>import('./views/404.vue')
    },
  ]

const router = location.host === "doudizhu.github.io"
  ? new Router({
    base: process.env.BASE_URL,
    routes,
  })
  : new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
  })

export default router
